import React, { useState, useEffect, useRef, lazy, Suspense } from "react";
import Header from "../../components/Header";
import Footer from "../../components/Footer"
import { Route, Routes, useParams } from "react-router-dom";
import useSWR from "swr";
import { getChapters } from "../../api/Courses";
import Breadcrumb from "../../components/Breadcrumb";
import { CgChevronDoubleDown } from 'react-icons/cg'
import { IoIosArrowDroprightCircle } from 'react-icons/io'
import Topics from './Topics'
import InvalidPage from "../InvalidPage";
import Skeleton from 'react-loading-skeleton'
import 'react-loading-skeleton/dist/skeleton.css'
import Helmet from "react-helmet";

const Topic = lazy(() => import("./Topic"))

function Chapters() {
    const [errorMessage, setErrorMessage] = useState("")
    const params = useParams()
    const id1 = params.id1
    const id2 = params.id2
    const [chapters, setChapters] = useState([])
    const { data, error } = useSWR(process.env.REACT_APP_BACKEND + `/chapters/${id1}/${id2}/listwithtopic`, async url => await getChapters(url), { revalidateOnFocus: false, revalidateOnReconnect: true })

    useEffect(() => {
        if (typeof data !== typeof undefined) {
            let chapters = data?.data?.chapters
            chapters = chapters.sort(function (a, b) { return a.sn - b.sn })
            chapters.map(chapter => { return { ...chapter, topics: chapter.topics.sort(function (a, b) { return a.sn - b.sn }) } })
            setChapters(data?.data?.chapters)
        }
        else if (error) {
            setErrorMessage(error?.response?.data?.message)
        }
    }, [data, error])

    return (
        <div className={`w-full h-full bg-back pt-20 dark:bg-background`}>
            <Helmet>
                <title>{`${data?.data?.subject?.title ? data?.data?.subject?.title + ' | ' : ""}Aakhyaan Academy`}</title>
            </Helmet>
            <Header page="programmes" />
            <Breadcrumb props={[{ header: "Programmes", link: "programmes" }, { header: data?.data?.course?.title, link: `programmes/${data?.data?.course?._id}` }, { header: data?.data?.subject?.title, link: `programmes/${data?.data?.course?._id}/${data?.data?.subject?._id}` }]} />
            {!data && !error ?
                <div className="mt-16 h-full">
                    <div className="dark:bg-background bg-back mt-10 font-sans text-lg w-full grid md:grid-cols-5 min-h-full">
                        <div className="mt-5 md:mt-0 hidden md:flex flex-col relative h-full w-full">
                            <div className="invisible md:visible min-w-[180px] dark:bg-formBox h-full md:flex flex-col bg-footer rounded-t-lg w-full">
                                <span className="mx-auto mb-1 place-self-center w-full px-2"><Skeleton height={25} baseColor='gray' /></span>
                                {[...Array(3)].map((val, i) =>
                                    <div className="flex flex-col items-center justify-center px-3 md:px-5 divide-y-2" key={i}>
                                        <div className="w-full"><Skeleton height={20} baseColor='pink' /></div>
                                    </div>
                                )}
                            </div>
                        </div>
                        <div className={`z-10 col-span-4 flex flex-col w-full h-fit p-5 px-10 dark:bg-background`}>
                            <div className="relative w-full flex flex-col gap-10">
                                <span className="absolute -top-14  md:-top-20 place-self-center"><Skeleton width={220} height={20} baseColor='gray' /></span>
                                <div className='flex flex-col gap-1 mt-10'>
                                    {[...Array(15)].map((val, i) =>
                                        <div key={i} className='w-full mx-auto'><Skeleton baseColor="gray" height={18} /></div>
                                    )}
                                </div>
                            </div >
                        </div >
                    </div>
                </div>
                :
                !error && data && data?.data?.chapters !== 0 ?
                    <div className="dark:bg-background bg-back mt-10 font-sans text-lg w-full lg:grid lg:grid-cols-5 min-h-full">
                        <Sidenav id1={id1} id2={id2} chapters={chapters} setErrorMessage={setErrorMessage} />
                        <Suspense
                            fallback={
                                <div className="flex flex-col text-white">
                                    <div className="m-auto">Loading...</div>
                                </div>
                            }>
                        </Suspense>
                        <Routes>
                            <Route path="*" element={<InvalidPage />} />
                            <Route path="/:chapterId/:topicId" element={<Topic />} />
                            <Route path="/" element={<Topic />} />
                        </Routes>
                    </div>
                    :
                    <>
                        {data && data?.data?.chapters === 0 ?
                            <>
                                <div className="text-warningText text-xl w-full h-full flex items-center justify-center">There are no chapters available at the moment.</div>
                                {console.log(data, chapters.length)}
                            </>
                            :
                            <></>
                        }
                        {error ?
                            <div className="text-warningText text-xl w-full h-full flex items-center justify-center">{errorMessage}</div>
                            :
                            <></>
                        }
                    </>

            }

            {/* <Footer /> */}
        </div>
    );
}

export default Chapters;

const Sidenav = ({ chapters, id1, id2, setErrorMessage }) => {
    const [chaptersPanelMode, setChaptersPanelMode] = useState(true)
    const chaptersPanel = useRef(null)
    const panelControlButton = useRef(null)
    const [isExpanded, setIsExpanded] = useState([true])
    const chaptersRef = useRef([])

    const expand = (i) => {
        let expanded
        if (isExpanded[i]) {
            expanded = isExpanded
            expanded[i] = false
            setIsExpanded([...expanded])
        }
        else if (!isExpanded[i] || typeof isExpanded[i] === typeof undefined) {
            expanded = isExpanded
            expanded[i] = true
            setIsExpanded([...expanded])
        }
    }

    const changeMode = (e) => {
        e.preventDefault()
        if (chaptersPanelMode) {
            setChaptersPanelMode(false)
        }
        else {
            setChaptersPanelMode(true)
        }
    }


    window.addEventListener("click", (e) => {
        if (!chaptersPanel.current?.contains(e.target) && chaptersPanel) {
            if (!panelControlButton.current?.contains(e.target) && panelControlButton) {
                if (!chaptersPanelMode) setChaptersPanelMode(true)
            }
        }
    })

    useEffect(() => {
        if (chaptersPanelMode) {
            chaptersPanel.current?.classList.remove("animate-moveRight", "lg:animate-none")
            chaptersPanel.current?.classList.add("animate-vanishLeft", "lg:animate-none")
            setTimeout(() => {
                chaptersPanel.current?.classList.add("invisible", "lg:animate-none")
            }, 500);
        }
        else {
            chaptersPanel.current?.classList.remove("invisible", "lg:animate-none")
            chaptersPanel.current?.classList.remove("animate-vanishLeft", "lg:animate-none")
            chaptersPanel.current?.classList.add("animate-moveRight", "lg:animate-none")
        }
    }, [chaptersPanelMode])

    return (
        <div className="z-20 lg:mt-0 flex flex-col relative">
            <span ref={panelControlButton} className={`lg:hidden fixed top-28 hover:cursor-pointer ${!chaptersPanelMode ? "rotate-180" : ""}`} onClick={changeMode}>
                <IoIosArrowDroprightCircle size={40} color="blue" />
            </span>
            <div ref={chaptersPanel} className="mt-5 invisible lg:visible min-w-[230px] lg:min-w-[180px] fixed left-0 select-none dark:bg-formBox h-full lg:flex flex-col bg-footer lg:relative rounded-tr-lg divide-y-2 dark:divide-blue-900 divide-blue-100 overflow-hidden z-30">
                {chapters?.map((chapter, i) =>
                    <div key={i} className="flex flex-col">
                        <div onClick={() => expand(i)} ref={element => chaptersRef.current.push(element)} className={`relative text-lg text-center hover:bg-blue-900 hover:text-mainText dark:hover:bg-slate-700 hover:opacity-90 hover:cursor-pointer  text-mainText flex items-center justify-center px-3 lg:px-5 border-none ${isExpanded[i] ? "shadow-lg dark:bg-background/60 bg-background/40" : "dark:bg-background bg-background/70 "} py-2`}>
                            <span>
                                {chapter?.title}
                            </span>
                            <span className={`absolute right-2 ${isExpanded[i] ? "rotate-180" : ""}`}>
                                <CgChevronDoubleDown />
                            </span>
                        </div>
                        {isExpanded[i] ?
                            <div className="animate-dropdownOpen">
                                <Topics setChaptersPanelMode={setChaptersPanelMode} subjectId={id2} chapterId={chapter._id} topics={chapter.topics} setErrorMessage={setErrorMessage} />
                            </div>
                            :
                            ""
                        }
                    </div>

                )}
            </div>
        </div>
    )
}
