import React, { useState, useEffect } from "react";
import { Link, useNavigate, Router } from "react-router-dom";

function Topics({ courseId, subjectId, chapterId, topics, setChaptersPanelMode, setErrorMessage }) {
    const navigate = useNavigate()
    let topicId = ""
    const splittedLoc = window.location.pathname.split('/')
    if (splittedLoc.length === 6) {
        topicId = splittedLoc[splittedLoc.length - 1]
    }
    useEffect(() => {
        if (topicId) navigate(`${chapterId}/${topicId}`, {replace:true})
        else if (topics[0]?._id) navigate(`${chapterId}/${topics[0]?._id}`, {replace:true})
        else setErrorMessage("There are no topics available at the moment.")
    }, [chapterId, navigate, topicId, topics])

    return (
        <div className="flex flex-col divide-y-[0.5px] divide-blue-200">
            {topics?.map((topic, i) =>
                <Link to={`${chapterId}/${topic?._id}`} onClick={(e) => {
                    e.preventDefault()
                    setChaptersPanelMode(true)
                    navigate(`${chapterId}/${topic?._id}`)
                }} key={i} className={`${topicId.split('/')[topicId.split('/').length - 1] === topic?._id ? "dark:bg-slate-600 bg-blue-600" : ""} text-mainText/90 text-lg text-center hover:underline hover:cursor-pointer flex flex-col items-center justify-center py-1`}>
                    {topic?.title}
                </Link>
            )}
        </div>
    );
}

export default Topics;